a { text-decoration:none !important } 

a:hover { text-decoration:none !important }

.App {
  text-align: center;
  box-sizing: border-box;
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: 100;
}

.guo-btn {
  font-family: 'Open Sans', sans-serif;
  font-weight : 300;
  font-size: 12px;
  margin-right: 2px;
  margin-left: 2px;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 6px;
  padding-bottom: 6px;
  border: 1px solid rgb(80, 80, 80);
  border-radius: 10px;
  color: rgb(80, 80, 80);
  background-color: rgba(255, 255, 255,0)
}

.guo-btn:hover {
  color: white;
  background-color: rgb(80, 80, 80);
  transition: 0.3s;
  -webkit-animation-name: shake;
          animation-name: shake;
  -webkit-animation-duration: 0.3s;
          animation-duration: 0.3s;
}

.mid-btn{
  font-size: 14px;
  margin: 10px;
  font-weight: 400;
  padding-left: 15px;
  padding-right: 15px;
}


.big-btn{
  font-size: 18px;
  margin: 10px;
  font-weight: 700;
  padding-left: 20px;
  padding-right: 20px;
}

.blue-btn{
  border: 1px solid rgb(27, 117, 188);
  color: rgb(27, 117, 188);
}

.blue-btn:hover {
  color: white;
  background-color: rgb(27, 117, 188);
}


.orange-btn{
  border: 1px solid orange;
  color: orange;
}

.orange-btn:hover {
  color: white;
  background-color: orange;
}


@-webkit-keyframes shake {
  0% {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
  25% {-webkit-transform: rotate(5deg);transform: rotate(5deg);}
  50% {-webkit-transform: rotate(-10deg);transform: rotate(-10deg);}
  75% {-webkit-transform: rotate(5deg);transform: rotate(5deg);}
  100% {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
}


@keyframes shake {
  0% {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
  25% {-webkit-transform: rotate(5deg);transform: rotate(5deg);}
  50% {-webkit-transform: rotate(-10deg);transform: rotate(-10deg);}
  75% {-webkit-transform: rotate(5deg);transform: rotate(5deg);}
  100% {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
}



section {
  margin-left: auto;
  margin-right: auto;
}

section p{
  margin: 5px;
}

section h3{
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: 700;
  font-size: 24px;
  text-align: left;
  margin-left: 30px;
  margin-top: 80px;
  margin-bottom: 30px;
}

section h4{
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: 700;
  font-size: 18px;
  text-align: left;
  margin-left: 30px;
  margin-top: 40px;
  margin-bottom: 20px;
}

input:focus {
  outline:none;
}

.toolkit-guide{
  margin-top: 30px;
  margin-bottom: 30px;
}


/* 헤더 */

.header {
  width: 100%;
  height: 60px;
  background-color: rgb(80, 80, 80);
  display: flex;
  justify-content: center;
  align-items: center;
}

nav a{
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: 700;
  color: white;
  text-decoration: none;
  font-size: 24px;
}


/* 헤더 */

/* 문제정의 섹션 */

.define h3{
  color: orange;
}

.where-text{
  height: 100px;
  border-radius: 12px;
  border: 0px;
  background-color: rgb(240, 240, 240);
  margin: 5px;
  padding: 10px;
}

.define-what{
  width: 700px;
  margin-left: auto;
  margin-right: auto;
}


.card-list{
  padding: 10px;
  border-radius: 5px;
  border-top-left-radius: 18px;
  border-top-right-radius: 18px;

  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;

  flex-wrap: wrap;
  margin-top: 20px;
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto;
  max-width: 650px;
}


.problem-card{
  position: relative;
  background-color: orange;
  width: 120px;
  height: 170px;
  border-radius: 12px;
  margin: 2px;
  z-index: 1;
}

.problem-card h5{
  color: white;
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: 700;
  font-size: 18px;

}

.problem-card img{
  width: 100px;
  margin-top: 20px;
}

.problem-card:hover{
  background-color: white;
  border: 2px solid orange;
  width: 120px;
  height: 170px;
  min-width: 120px;
  height: 170px;
  border-radius: 12px;
  -webkit-transform: scale(1.4);
          transform: scale(1.4);
  z-index: 10;
  cursor: pointer;
}

.problem-card-back{
  display: none;
}

.problem-card-back h5{
  display: none;
  color: orange;
}

.card-wrap{
  width: 100%;
  height: 100%;
  position: absolute;
}

.card-wrap:hover .problem-card-back{
  display: block;
  color: gray;
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: 700;
  font-size: 11px;
  position: relative;
  padding: 5px;
}

.card-wrap:hover .problem-card-front{
  display: none;
}

.card-wrap:hover .problem-card-back h5{
  display: block;
  color: orange;
}

.card-box{
  height: 250px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  margin-top: 30px;
  margin-bottom: 30px;

}

.card-slot{
  background-color: rgb(230, 230, 230);
  width: 150px;
  height: 230px;
  min-width: 150px;
  height: 230px;
  border-radius: 12px;
  margin: 2px;
  position: relative;
  margin-left: auto;
  margin-right: auto;

}

.card-slot p {

  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);

}


/* 5whys 탭 */


.why-box{
  height: 200px;
}

.define-why input{
  text-align: center;
  background-color: rgb(240, 240, 240);
  border: none;
  border-radius: 20px;
  height: 40px;
  margin: 20px;
  width: 400px;
}



